import axios from '../axios'

/*
 * 
 */

// 
export const findPage = (params) => {
    return axios({
        url: '/system/operationLog/list',
        method: 'get',
        params
    })
}

// 
export const getOperationType = () => {
    return axios({
        url: '/system/operationType/list',
        method: 'get'
    })
}


